body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  color: #000;
  font-weight: 400;
  min-width: 320px;
  overflow-x: hidden;
  transition: 1s ease;
  height: 100%;
}

html{
  height: 100%;
  transition: 1s ease;
}

h1,
h2,
h3 {
  text-transform: uppercase;
  margin: 0;
  color: #000;
  font-weight: 700;
}

.sect__title {
  margin-bottom: 35px;
  text-align: center;
}

.first-title {
  font-size: 48px;
}

.second-title {
  font-size: 40px;
  color: $accent;
}

.third-title {
  text-transform: none;
  font-size: 18px;
  margin-bottom: 6px;
}

ul, li {
  margin: 0;
  padding: 0;
  display: block;
}

p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }
}

header {
  position: relative;
}

section {
  position: relative;
  padding-top: 50px;
  padding-bottom: 48px;
}

footer {
  position: relative;
  height: 177px;
}

.flex {
  display: flex;
}